import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import UniversityProjects from './UniversityProjects';

interface TabPanelProps {
  children?: React.ReactNode;
  index: number;
  value: number;
}

function CustomTabPanel(props: TabPanelProps) {
  const { children, value, index, ...other } = props;

  return (
    <div 
      role="tabpanel"
      hidden={value !== index}
      id={`simple-tabpanel-${index}`}
      aria-labelledby={`simple-tab-${index}`}
      {...other}
    >
      {value === index && <Box sx={{ p: 3 }}>{children}</Box>}
    </div>
  );
}

function a11yProps(index: number) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

function ProjectTabs() {
  const [value, setValue] = React.useState(0);

  const handleChange = (event: React.SyntheticEvent, newValue: number) => {
    setValue(newValue);
  };

  return (
    <Box sx={{ width: '100%' }}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider', fontFamily:'Maple Mono' }}>
        <Tabs  className='PopOut' value={value} onChange={handleChange} indicatorColor="secondary" textColor="secondary" aria-label="basic tabs example" >
          <Tab sx={{ '&:hover': {backgroundColor: 'white'} }} label="University Projects" {...a11yProps(0)} />
          <Tab sx={{ '&:hover': {backgroundColor: 'white'} }} label="Personal Projects" {...a11yProps(1)} />
        </Tabs>
      </Box>
      <CustomTabPanel value={value} index={0}>
        <UniversityProjects />
      </CustomTabPanel>
      <CustomTabPanel value={value} index={1}>
        <p className='TextContents'>Coming Soon...</p>
      </CustomTabPanel>
    </Box>
  );
}

export default ProjectTabs;